import React from 'react'
import {
  Card,
  CardActions,
  CardHeader,
  CardMedia,
  CardTitle,
  CardText,
} from 'material-ui/Card'
import { Grid, Button } from 'material-ui'
import Avatar from 'material-ui/Avatar'
import { Mixins, Styles } from 'material-ui'
import * as colors from 'material-ui'
import Paper from 'material-ui/Paper'
import Typography from 'material-ui/Typography'
import Link from 'gatsby-link'

const styles = {
  backgroundColor: '#EEEEEE',

  div: {
    display: 'flex',
    flexDirection: 'row wrap',
    justifyContent: 'center',
    // padding: 20,
    // width: '100%',
    // width: 1200,
    // margin: 'auto',
    // marginTop:6,
  },

  divLeft: {
    display: 'flex',
    flexDirection: 'row wrap',
    justifyContent: 'center',
    // padding: 20,
    // width: '100%',
    // width: 1200,
    // margin: 'auto',
    // marginTop:6,
  },
}

let arr = ['sss', 'qqq', 'eee']
let indents = ['sss', 'qqq', 'eee']

class kitchenlive extends React.Component {
  constructor(props) {
    super(props)
    // console.log(props.params.id);
  }
  render() {
    return (
      <Paper elevation={4}>
        <div style={{ margin: 14 }}>
          <Typography variant="Subheading" color="secondary" component="h2">
            KitchenLive
          </Typography>{' '}
          <br />
          <Typography component="p" color="textSecondary">
            키친라이브
            <br />
            <br />
            실시간 화상 요리 교육 플랫폼 <br />
            원하는 날짜와 요리를 신청하면 식자재가 배송되고 원하는 날짜에
            쉐프님과 실시간으로 요리를 배울 수 있다. <br />
            React Native 로 만들었으며, WebRTC + TURN + Media Server 를 사용,{' '}
            <br />
            실시간으로 약 100명 정도 동시에 화상회의가 가능하다. <br />
            현재 한국에서 하나밖에 없는 원격 실시간 화상 요리 교육 플랫폼이다.{' '}
            <br />
            <br />
            홈페이지 <br />
            <a href="https://kitchenlive.tv">https://kitchenlive.tv</a>
            <br />
            <br />
            안드로이드 앱 <br />
            <a href="https://play.google.com/store/apps/details?id=com.kitchenlivereactnative">
              Android App 다운받기
            </a>
            <br />
            <br />
            Medium 블로그 <br />
            <a href="https://medium.com/@itstedpark/webrtc-janus-react-native-%EA%B5%AC%ED%98%84%ED%95%98%EA%B8%B0-get-hands-dirty-c23d428a3f70">
              Medium Link
            </a>
            <br />
            <br />
            {/* <iframe src="https://player.vimeo.com/video/264843575" width="800" height="452" frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe> */}
            <div style={{ padding: '56.25% 0 0 0', position: 'relative' }}>
              <iframe
                src="https://player.vimeo.com/video/264843575"
                style={{
                  position: 'absolute',
                  top: 0,
                  left: 0,
                  width: '100%',
                  height: '100%',
                }}
                frameBorder={0}
                allowFullScreen
              />
            </div>
            <script src="https://player.vimeo.com/api/player.js" />
            <br />
            <br />
            1. WebRTC 를 알게 된건 와이프님의 “실시간 요리 방송 서비스를
            만들어줘” 에서 출발했다.<br />
            <br />
            2. 처음엔 가볍게 Socket.io 에 node.js 붙이고 스마트폰 디바이스에서
            동영상 압축해서 서버로 보낼 생각이였다.<br />
            <br />
            3. 하지만 나의 기대와는 다르게 3초 이상씩 동영상이 딜레이 되었다.<br />
            <br />
            4. 그리고 WebRTC 를 알게 되었고, 서버 없이 1:1 은 큰 문제 없이 구현
            되었다.<br />
            <br />
            5. WebRTC 를 구현하면서 STUN 과 TURN 에 대해 구별하고 이해하고 구현
            할 수 있었다.<br />
            <br />
            6. 다시 시작된 와이프의 요청 사항 “여러명이서 할 수는 없나?”<br />
            <br />
            7. 다시 시작된 digging 중 SFU 와 MCU 에 대해 알게 되었고, 미디어
            서버에 관한 힌트를 얻을 수 있었다.<br />
            <br />
            <br />
            ==================== 최종 구현은 아래와 같다.
            =======================<br />
            <br />
            <br />
            iOS , Android , Web 에서 모두 동작하는 실시간 쌍방향 비디오 패킷을
            udp 방식으로 쏘고, <br />
            <br />
            코덱은 가변적인 vp9 스마트폰은 h.264를 쓰도록 구현했다.<br />
            <br />
            프로토콜은 WebRTC.<br />
            <br />
            미디어 서버는 홍콩이나 싱가폴에 있는 Azure 에 Docker 로 scalability
            하게 구현했다.<br />
            <br />
            모든 부하는 미디어 서버가 담당하고, 클라이언트는 그저 자신의
            미디어만 미디어서버로 전송한다.<br />
            <br />
            미디어 서버는 모든 패킷을 녹화하고 저장한다. <br />
            <br />
            각 room 은 쌍방향은 100명이상, <br />
            <br />
            단순 미디어 스트리밍은 10000명이상.<br />
            <br />
            처리하도록 설계되었다.<br />
            <br />
            보도자료 <br />
            <a href="http://it.donga.com/26454/">보도자료 Link</a>
          </Typography>
        </div>
      </Paper>
    )
  }
}

export default kitchenlive



